import React, { useEffect, useState } from 'react';
import DetailMap from './detail_map';
import styles from './festival_detail.module.css'

const FestivalDetail = ({festivals}) => {

    const [details, setDetails] = useState('')

    useEffect(() => {
        festivals.contentInfo().then(data => setDetails(data))
    }, [])

    const {overview, firstimage, firstimage2, hompage, mapx, mapy, tel, title, zipcode} = festivals

    return (
            <section className={styles.section}>
                <p>{details.overview}</p>

                <DetailMap />
            </section>
    );}

export default FestivalDetail;